<template>
  <div class="page page-user-bill">
    <div class="hd border_bottom_1px">
      <div class="item">账变时间</div>
      <div class="item">
        <van-dropdown-menu>
          <van-dropdown-item ref="bill_type" title="账变类型">
            <van-radio-group v-model="form.type" @change="onChange">
              <van-radio v-for="(v, k) in types" :key="k" :name="k" class="btn">
                <van-button slot="icon" class="btn" @click="clearSelect(k)">
                  {{ v }}
                </van-button>
              </van-radio>
            </van-radio-group>
          </van-dropdown-item>
        </van-dropdown-menu>
      </div>
      <div class="item">账变金额</div>
    </div>
    <van-list
      ref="list"
      v-model="loading"
      :error-text="errorText"
      :error.sync="error"
      :finished="finished"
      finished-text="暂无更多数据"
      @load="onLoad"
    >
      <div
        v-for="(item, index) in list"
        :key="index"
        class="row border_bottom_1px"
        @click="$router.push(`/user_bill_detail?id=${item.id}`)"
      >
        <div class="item">
          {{ item.createTime | dateFormat("Y/M/D h:m:s") }}
        </div>
        <div class="item">{{ item.type_name }}</div>
        <div class="item">{{ parseFloat(item.amount).toFixed(2) }}</div>
      </div>
    </van-list>

    <van-action-sheet
      v-model="showAction"
      :actions="actions"
      safe-area-inset-bottom
      @select="onSelect"
    />
    <van-calendar
      v-model="showCalendar"
      :max-date="maxDate"
      :min-date="minDate"
      safe-area-inset-bottom
      type="range"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>

import {Calendar} from 'vant'
import {Change, getChangeType} from "@/apis/user";
import {dateFormat, moment} from "@/vendor/tools";

export default {
  components: {
    [Calendar.name]: Calendar,
  },
  data() {
    return {
      showAction: false,
      actions: [
        {name: "今日", value: "today"},
        {name: "近三天", value: "d3"},
        {name: "近五天", value: "d5"},
        {name: "自定义", value: "custom"},
        {name: "取消", value: "cancel"},
      ],
      minDate: new Date(2022, 0, 1),
      maxDate: new Date(),
      types: {},
      form: {
        startTime: moment()
          .hour("0")
          .minute("0")
          .second("0")
          .format("Y-M-D h:m:s"),
        endTime: moment()
          .hour("23")
          .minute("59")
          .second("59")
          .format("Y-M-D h:m:s"),
        type: "",
        page: 1,
        pageSize: 20,
      },
      list: [],
      loading: false,
      error: false,
      finished: false,
      errorText: "加载失败，点击重试",
      showCalendar: false,
    };
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back", "time-picker"], {
      down: true,
      label: "今天",
      onClick: this.onClickTime,
    });
    this.loadChangeType();
  },
  methods: {
    onSelect(item) {
      if (item.value == "custom") {
        this.showCalendar = true;
      } else {
        this.$root.topConfig.label = item.name;
        this.$root.topConfig.down = true;

        this.form.endTime = moment()
          .hour("23")
          .minute("59")
          .second("59")
          .format("Y-M-D h:m:s");

        if (item.value == "today")
          this.form.startTime = moment()
            .hour("0")
            .minute("0")
            .second("0")
            .format("Y-M-D h:m:s");

        if (item.value == "d3")
          this.form.startTime = moment()
            .day(-3)
            .hour("0")
            .minute("0")
            .second("0")
            .format("Y-M-D h:m:s");

        if (item.value == "d5")
          this.form.startTime = moment()
            .day(-5)
            .hour("0")
            .minute("0")
            .second("0")
            .format("Y-M-D h:m:s");

        this.onChange();
      }
      this.showAction = false;
    },
    onConfirm(date) {
      const [start, end] = date;
      this.form.startTime = dateFormat(start, "Y-M-D h:m:s");
      this.form.endTime = moment(end)
        .hour("23")
        .minute("59")
        .second("59")
        .format("Y-M-D h:m:s");
      this.showCalendar = false;
      this.$root.topConfig.label = "自定义";
      this.$root.topConfig.down = true;
      this.onChange();
    },
    onClickTime() {
      this.showAction = true;
      this.$root.topConfig.down = false;
    },
    onLoad() {
      Change(
        this.$root.$tls.processParams(
          {
            ...this.form,
          },
          ["shake"]
        )
      ).then(({ok, data, msg}) => {
        if (ok) {
          if (this.form.page === 1) {
            this.list = data.list;
          } else {
            this.list.push(...data.list);
          }
          this.loading = false;
          this.finished = this.form.page * this.form.pageSize >= data.total;
          this.form.page += 1;
        } else {
          this.error = true;
          this.errorText = msg;
          this.$toast.fail(msg);
        }
      });
    },
    loadChangeType() {
      getChangeType().then(({ok, data, msg}) => {
        if (ok) {
          this.types = data;
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    onChange() {
      this.$refs.bill_type.toggle(false);
      this.form.page = 1;
      this.list = [];
      this.onLoad();
    },
    clearSelect(v) {
      if (this.form.type === v) {
        setTimeout(() => (this.form.type = ""), 50);
      }
    },
  },
};
</script>

